@import "../../styles/colors.scss";

.covid-statistics {
  margin-bottom: 50px;

  &__main-section {
    display: flex;
    justify-content: center;
  }

  &__heading {
      margin-top: 15px;
      font-size: 24px;
  }

  &__stat {
    background-color: $secondary-blue-color;
    color: $secondary-light-color;
    margin: 15px;
    padding: 10px;
    border-radius: 4px;
    filter: drop-shadow(-1px 1px 0.08rem $shadow-color);
  }
}
